<template>
  <div  class="detail-cover-wrap">

    <div id="detail-top-nav">
      <div>
        <div class="col-md-12">
         <div class="col-xs-2">
          <h1 >
           <span id="back_home" @click="showHome" class="fa fa-bars" style="margin-right:20px;"></span>
          </h1>
         </div>

         <div class="col-xs-9">
          <h1 class="name-title pull-right">{{detailStaged.first_name}} {{detailStaged.last_name}}</h1>

         </div>
         <div class="col-xs-1">
          <a class="edit-contact pull-right" @click="initEditContact()"><span class="fa fa-edit"></span></a>
         </div>
        </div>
      </div>
    </div>

    <div style="padding-top:60px;">

       <!--this makes up the whole meat of the area. needs to be fixed so that it is reactive to the vuex state.-->
        <div class="col-md-12">

         <p class="birthday-title">
          <span class="fa fa-gift"></span>: {{detailStaged.birthday}}
         </p>

          <InfoDetailList :options="{'type':'email'}">
          </InfoDetailList>

          <InfoDetailList :options="{'type':'phone'}">
          </InfoDetailList>

          <InfoDetailList :options="{'type':'address'}">
          </InfoDetailList>


          <div class="col-md-12" style="margin-top:20px;margin-bottom:20px;">
           <a href="#" class="btn delete-btn" @click="initDelete()" >Delete Contact</a>
          </div>

        </div>

    </div>
  </div>

</template>

<script>
import store from '../store/store.js'
import InfoDetailList from './InfoDetailList.vue'
export default {
 components:{
  InfoDetailList,
 },
  computed:{
      detailStaged: {
        get(){
          return store.getters.detailStaged
        }
    }
  },
  methods:{
    showHome(e){
      store.dispatch('LOAD_HOME_STAGE')
    },
    initEditContact(){
     let details = {
      'type':'contact',
      'action':'update',
      'detail': this.detailStaged
     };
     store.dispatch("LOAD_KITCHEN_SINK", details );

    },

    initDelete(){
     let details = {
      'type':'contact',
      'action':'delete',
      'detail': this.detailStaged
     };
     store.dispatch("LOAD_KITCHEN_SINK", details );
    }
  }
}
</script>


<style lang="scss">
@import 'colors';

.detail-cover-wrap{
  .name-title{
   display:block;
   margin-right:10px;
  }
  .birthday-title{
   padding-top:10px;
   display:block;
   font-size:15px;
   margin-left:20px;
  }

  #back_home{
    font-size:20px;
    display:block;
    cursor:pointer;
  }


  #detail-top-nav{
   -webkit-box-shadow: 0px 0px 5px 0px $box-shadow-color;
   -moz-box-shadow: 0px 0px 5px 0px $box-shadow-color;
   box-shadow: 0px 0px 5px 0px $box-shadow-color;
   background-color:$kitchen-sink;
    .edit-contact{
     font-size:25px;
     margin-top:15px;
     cursor:pointer;
     color:$font-color;
    }
    z-index:6;
    background-color:$primary-mid;
    position:fixed;
    width:100%;
    margin-right:0px;
    margin-left:0px;
  }
  -webkit-overflow-scrolling: touch;
  overflow-y:scroll;
  overflow-x:hidden;
  h1{
    font-size:20px;
  }
  .close-detail{
    color:$font-color;
    font-size:30px;
    float:right;
    margin:10px;
  }
  z-index:4;
  background-color: $base;
  color:$font-color;
  width:100%;
  height:100%;
  position:fixed;
  top:0px;
  left:0px;
}
</style>



