
<template>

  <div v-if="options.action == 'delete'" class="stnd-form-wrap">
   <ul class='error-list'>
    <li v-for="error in errors" class="error">
      {{error}}
    </li>
   </ul>
   <form   style="text-align:center;">

     <label>Are You Sure You Want To Delete {{detail.first_name}} {{detail.last_name}} From Your Contacts? </label>
     <br>
     <input v-on:click.prevent="submitContact" class="btn btn-danger" type="submit" value="Yes, Delete This Contact" />
   </form>

  </div>
  <div v-else id="new_contact_panel" class="stnd-form-wrap">
    <h2 v-if="detail.id" id="c-form-title">Edit Contact</h2>
    <h2 v-else id="c-form-title">Add New Contact</h2>

    <ul class='error-list'>
     <li v-for="error in errors" class="error">
       {{error}}
     </li>
    </ul>

    <div v-if="loading" class="animated-loader">Loading...</div>
    <form v-else v-on:submit.prevent="submitContact" >
      <label>First Name</label>
      <input v-model="detail.first_name" type="text" class="form-control" >
      <label>Last Name</label>
      <input v-model="detail.last_name" type="text" class="form-control" >
      <label>Birthday</label>
      <input v-model="detail.birthday" type="text" class="form-control" placeholder="Format: yyyy-mm-dd">
      <br>
      <input type="submit" value="Save" class="btn btn-default">
    </form>
  </div>


</template>


<script>
import store from '../../store/store.js'
import axios from 'axios'
import ContentApi from '../../api.js'
const contentApi = new ContentApi();

export default {
  props:{
    options:{
      type: Object,
      required: true
    },
  },
  data () {
     return {
       detail:this.options.detail,
       loading:false,
       errors:[],
     }
   },
  methods: {
    async postSuccess(response){
     await store.dispatch('LOAD_CONTACTS_LIST');
     this.stopLoad();
     store.dispatch('LOAD_DETAIL_STAGE', response.data.id);
     store.dispatch('LOAD_KITCHEN_SINK', false);
    },
    postFailure(error){
     this.stopLoad();
     this.errors = [];
     this.errors.push('Something Went Wrong. Try Again.');
    },

    deleteSuccess(id){
     //should simply delete the user with the staged id from the store contacts.
     store.dispatch('LOAD_DETAIL_STAGE', false);
     store.dispatch('LOAD_KITCHEN_SINK', false);
    },

    deleteFailure(){
     this.stopLoad();
     this.errors = [];
     this.errors.push('Something Went Wrong. Try Again.');
     //should simply show an error message saying something went wrong.
    },
    putSuccess(response){
     store.dispatch('REPLACE_CONTACT', response.data);
     store.dispatch('LOAD_KITCHEN_SINK', false);
     //this should load the mutated contact to stage.
     store.dispatch('LOAD_DETAIL_STAGE', response.data.id);
    },
    putFailure(){
     this.stopLoad();
     this.errors = [];
     this.errors.push('Something Went Wrong. Try Again.');
    },

    load(){
     this.loading = true;
    },
    stopLoad(){
     this.loading = false;
    },

    validateForm(){
     if(this.detail.first_name == "" || this.detail.last_name == "" || this.detail.birthday == ""){
      this.errors = []
      this.errors.push('All fields must be filled out.')
      return false;
     }
     else{
      //check that it is using "-" as limit
      let dateSplit = this.detail.birthday.split('-');
      let correctLimiter = dateSplit.length == 3;
      let correctLength = dateSplit[0].length == 4 && dateSplit[1].length == 2 && dateSplit[2].length == 2;

      if(Date.parse(this.detail.birthday) && correctLimiter && correctLength){
       this.errors = [];
       return true;
      }
      else{
       this.errors = ['Birthday format is incorrect. It must be yyyy-mm-dd'];
       return false;
      }
     }
    },

    async createContact(){
     let data = {
      'first_name':this.detail.first_name,
      'last_name':this.detail.last_name,
      'birthday':this.detail.birthday
     }
     let resp = await store.dispatch('CREATE_CONTACT', data);
     if(resp.status == 201){
      this.postSuccess(resp);
     }
     else{
      this.postFailure(resp);
     }
    },

    async deleteContact(){
     let theId = this.detail.id;
     let resp = await store.dispatch('DELETE_CONTACT', theId);
     if(resp.status == 204){
      this.deleteSuccess(theId);
     }
     else{
      this.deleteFailure();
     }
    },

    async updateContact(){
     let resp = await store.dispatch('UPDATE_CONTACT', this._data)
     console.log(resp)
     if(resp.status == 200){
      this.putSuccess(resp);
     }
     else{
      this.putFailure();
     }
    },

    handleCorrectMethod(){
     //based on what the action is set to, run the correct axios function.
     if(this.options.action == 'create'){
      this.createContact();
     }
     if(this.options.action == 'delete'){
      this.deleteContact();
     }
     if(this.options.action == 'update'){
      this.updateContact();
     }
    },
    submitContact(){
      if(this.validateForm()){
       this.load();
       this.handleCorrectMethod();
      }
    },
  }
}
</script>

<style lang="scss">
@import '../colors';
.animated-loader,
.animated-loader:before,
.animated-loader:after {
  background: $primary-contrast;
  -webkit-animation: load1 1s infinite ease-in-out;
  animation: load1 1s infinite ease-in-out;
  width: 1em;
  height: 4em;
}
.animated-loader {
  color: $primary-contrast;
  text-indent: -9999em;
  margin: 88px auto;
  position: relative;
  font-size: 11px;
  -webkit-transform: translateZ(0);
  -ms-transform: translateZ(0);
  transform: translateZ(0);
  -webkit-animation-delay: -0.16s;
  animation-delay: -0.16s;
}
.animated-loader:before,
.animated-loader:after {
  position: absolute;
  top: 0;
  content: '';
}
.animated-loader:before {
  left: -1.5em;
  -webkit-animation-delay: -0.32s;
  animation-delay: -0.32s;
}
.animated-loader:after {
  left: 1.5em;
}
@-webkit-keyframes load1 {
  0%,
  80%,
  100% {
    box-shadow: 0 0;
    height: 4em;
  }
  40% {
    box-shadow: 0 -2em;
    height: 5em;
  }
}
@keyframes load1 {
  0%,
  80%,
  100% {
    box-shadow: 0 0;
    height: 4em;
  }
  40% {
    box-shadow: 0 -2em;
    height: 5em;
  }
}


</style>



