
<template>
<div >
 <div v-if="options.type == 'email'" class="col-md-12 stnd-form-wrap">
  <h2 v-if="options.id" id="c-form-title">Edit Email</h2>
  <h2 v-else id="c-form-title">New Email</h2>

  <ul class='error-list'>
   <li v-for="error in errors" class="error">
     {{error}}
   </li>
  </ul>

  <div v-if="loading" class="animated-loader">Loading...</div>
  <div  v-else>
   <form v-on:submit.prevent="submitContactInfos()">
    <input type="text" placeholder="example@email.com" v-model="value" class="form-control">
    <br>
    <input type="submit" value="submit" class="btn btn-default">
   </form>
  </div>
  <br>
  <br>
  <br>
  <br>

  <a v-if="options.id"  class="btn btn-danger del-btn" @click=deleteInfos()>Delete This Email</a>
 </div>


 <div v-if="options.type == 'phone'" class="col-md-12 stnd-form-wrap">

  <h2 v-if="options.id" id="c-form-title">Edit Phone Number</h2>
  <h2 v-else id="c-form-title">New Phone Number</h2>
  <ul class='error-list'>
   <li v-for="error in errors" class="error">
     {{error}}
   </li>
  </ul>

  <form v-on:submit.prevent="submitContactInfos()">
   <input type="text" placeholder="(210) 555-5555" v-model="value" class="form-control">
   <br>
   <input type="submit" value="submit" class="btn btn-default">
  </form>
  <div v-if="loading" class="animated-loader">Loading...</div>
  <br>
  <br>
  <br>
  <a v-if="options.id"  class="btn btn-danger del-btn" @click=deleteInfos()>Delete This Phone Number</a>
 </div>


</div>
</template>

<script>
import store from '../../store/store.js'
import axios from 'axios'
import ContentApi from '../../api.js'
const contentApi = new ContentApi();

export default {
  props:{
    options:{
      type: Object,
      required: true
    },
  },
  data () {
     return {
       detail:this.options.detail,
       loading:false,
       errors:[],
       id: this.options.id,
       value:this.options.value,
       medium: this.options.type
     }
   },
  methods: {
   load(){
    this.loading = true;
   },
   stopLoad(){
    this.loading = false;
   },

   async postSuccess(response){
    try{
     let resp = await store.dispatch('LOAD_CONTACTS_LIST');
     store.dispatch('LOAD_DETAIL_STAGE', this.detail.id);
     store.dispatch('CLEAR_KITCHEN_SINK');
    }
    catch(e){
     console.log(e)
    }
   },

   async putSuccess(response){
    try{
     let resp = await store.dispatch('LOAD_CONTACTS_LIST');
     store.dispatch('LOAD_DETAIL_STAGE', this.detail.id);
     store.dispatch('CLEAR_KITCHEN_SINK');
    }
    catch(e){
     console.log(e)
    }
   },

   async deleteSuccess(){
    try{
     let resp = await store.dispatch('LOAD_CONTACTS_LIST');
     store.dispatch('LOAD_DETAIL_STAGE', this.detail.id);
     store.dispatch('CLEAR_KITCHEN_SINK');
    }
    catch(e){
     console.log(e)
    }
   },

   async createInfos(){
     let resp = await store.dispatch('CREATE_CONTACT_INFOS', this)
     if(resp.response){
      this.stopLoad();
     }
     else{
      this.postSuccess(resp);
      this.stopLoad();
     }
   },

   async deleteInfos(){
    let resp = await store.dispatch('DELETE_CONTACT_INFOS', this)
    this.deleteSuccess(resp);
    this.load()
   },
   async updateInfos(){
    let resp = await store.dispatch('UPDATE_CONTACT_INFOS', this)
    this.putSuccess(resp);
    this.stopLoad();
   },

   handleCorrectMethod(){
    console.log(this)
    //based on what the action is set to, run the correct axios function.
    if(this.options.id){
     console.log("handle method is triggering")
     this.updateInfos();
    }
    else{
     this.createInfos();
    }
   },

   submitContactInfos(){
    if(this.value == ""){
     this.errors = ["This field can not be blank"]
    }
    else{
     this.load();
     this.handleCorrectMethod();
    }
  }
 }
}
</script>

<style lang="scss">
@import '../colors';
.animated-loader,
.animated-loader:before,
.animated-loader:after {
  background: $primary-contrast;
  -webkit-animation: load1 1s infinite ease-in-out;
  animation: load1 1s infinite ease-in-out;
  width: 1em;
  height: 4em;
}
.animated-loader {
  color: $primary-contrast;
  text-indent: -9999em;
  margin: 88px auto;
  position: relative;
  font-size: 11px;
  -webkit-transform: translateZ(0);
  -ms-transform: translateZ(0);
  transform: translateZ(0);
  -webkit-animation-delay: -0.16s;
  animation-delay: -0.16s;
}
.animated-loader:before,
.animated-loader:after {
  position: absolute;
  top: 0;
  content: '';
}
.animated-loader:before {
  left: -1.5em;
  -webkit-animation-delay: -0.32s;
  animation-delay: -0.32s;
}
.animated-loader:after {
  left: 1.5em;
}
@-webkit-keyframes load1 {
  0%,
  80%,
  100% {
    box-shadow: 0 0;
    height: 4em;
  }
  40% {
    box-shadow: 0 -2em;
    height: 5em;
  }
}
@keyframes load1 {
  0%,
  80%,
  100% {
    box-shadow: 0 0;
    height: 4em;
  }
  40% {
    box-shadow: 0 -2em;
    height: 5em;
  }
}

.del-btn{
 width:100%;
}

</style>



