<template>
  <li @click="showDetail(contact.id, $event)" class="contact-item" >
    {{contact.first_name}} {{contact.last_name}}
    <span class="fa fa-info-circle info-btn"></span>

  </li>
</template>

<script>
import store from '../store/store.js'
export default {
  props:{
    contact:{
      type: Object,
      required: true
    }
  },
  methods:{
    showDetail:(id, e)=>{
      //set the detail stage.
      store.dispatch('LOAD_DETAIL_STAGE', id);
    }
  }
}
</script>

<style lang="scss">

@keyframes slideInFromTop {
  0% {
    transform: translateY(-100%);
  }
  100% {
    transform: translateY(0);
  }
}

.thing-stuff{
  background-color:green;
}
.contact-item{
 animation: .5s ease-out 0s 1 slideInFromTop;
}
</style>



