email
<template>
<div class="col-md-12">
 <div v-if="loading" class="animated-loader">Loading...</div>
 <div v-else class="stnd-form-wrap">
  <h1 v-if="id" >Edit Address</h1>
  <h1 v-else >New Address</h1>

  <ul class='error-list'>
   <li v-for="error in errors" class="error">
     {{error}}
   </li>
  </ul>
  <form v-on:submit.prevent="submitAddress" >
    <label>Address Line 1</label>
    <input  v-model="address_line1" type="text" class="form-control" >
    <label>Address Line 2</label>
    <input v-model="address_line2"  type="text" class="form-control" >
    <label>Postal Code</label>
    <input v-model="postal_code"  type="text" class="form-control" >
    <label>City</label>
    <input v-model="city"  type="text" class="form-control" >
    <label>State</label>
    <input v-model="state_province"  type="text" class="form-control" >
    <label>Country</label>
    <input v-model="country"  type="text" class="form-control" >
    <br>
    <input type="submit" value="Save" class="btn btn-default">
  </form>
  <br>
  <a v-if="id"  class="btn btn-danger del-btn" @click=deleteAddress()>Delete This Address</a>

 </div>


</div>
</template>

<script>
import store from '../../store/store.js'
import axios from 'axios'
import ContentApi from '../../api.js'
const contentApi = new ContentApi();

export default {
  props:{
    options:{
      type: Object,
      required: true
    },
  },

  data () {
     return {
       detail:this.options.detail,
       loading:false,
       address_line1:"",
       address_line2:"",
       postal_code:"",
       city:"",
       state_province:"",
       country:"",
       id:false,
       errors:[],
     }
   },
  mounted(){
   if(this.options.address){
    this.address_line1 = this.options.address.address_line1
    this.address_line2 = this.options.address.address_line2
    this.postal_code = this.options.address.postal_code
    this.city = this.options.address.city
    this.state_province = this.options.address.state_province
    this.country = this.options.address.country
    this.id = this.options.address.id
   }
  },
  methods: {
   load(){
    this.loading = true;
   },
   stopLoad(){
    this.loading = false;
   },
   async postSuccess(){
    try{
     let resp = await store.dispatch('LOAD_CONTACTS_LIST');
     store.dispatch('LOAD_DETAIL_STAGE', this.detail.id);
     store.dispatch('CLEAR_KITCHEN_SINK');
    }
    catch(e){
     console.log(e)
    }
   },
   async putSuccess(){
    try{
     let resp = await store.dispatch('LOAD_CONTACTS_LIST');
     store.dispatch('LOAD_DETAIL_STAGE', this.detail.id);
     store.dispatch('CLEAR_KITCHEN_SINK');
    }
    catch(e){
     console.log(e)
    }
   },

   async deleteSuccess(){
    try{
     let resp = await store.dispatch('LOAD_CONTACTS_LIST');
     store.dispatch('LOAD_DETAIL_STAGE', this.detail.id);
     store.dispatch('CLEAR_KITCHEN_SINK');
    }
    catch(e){
     console.log(e)
    }
    //what do you want to happen on a successful deletion?
   },

   async createAddress(){
    let resp = await store.dispatch('CREATE_ADDRESS', this)

    if(resp.response){
     this.postFailure();
     this.stopLoad();
    }
    else{
     this.postSuccess();
     this.stopLoad();
    }

   },
   async deleteAddress(){
    this.load()
    let resp = await store.dispatch('DELETE_ADDRESS', this)

    if(resp.response){
     this.deleteFailure();
     this.stopLoad();
    }
    else{
     this.deleteSuccess();
     this.stopLoad();
    }
    //implement axios to delete an email record
   },
   async updateAddress(){
    let resp = await store.dispatch('UPDATE_ADDRESS', this)
    this.putSuccess()
   },
   handleCorrectMethod(){
    //based on what the action is set to, run the correct axios function.
    if(this.id){
     this.updateAddress();
    }
    else{
     this.createAddress();
    }

   },
   validate(){
    if(
     this.address_line1 == "" ||
     this.address_line2 == "" ||
     this.postal_code == "" ||
     this.city == "" ||
     this.state_province == "" ||
     this.country == ""){
      this.errors = ['You can not leave any fields blank.']
      return false;
    }
    else{
     this.errors = []
     return true;
    }
   },
   submitAddress(){
    if(this.validate()){
     this.load();
     this.handleCorrectMethod()
    }
    //simply test that this is triggered when it needs to be.
   }
 }
}
</script>

<style lang="scss">
@import '../colors';
.animated-loader,
.animated-loader:before,
.animated-loader:after {
  background: $primary-contrast;
  -webkit-animation: load1 1s infinite ease-in-out;
  animation: load1 1s infinite ease-in-out;
  width: 1em;
  height: 4em;
}
.animated-loader {
  color: $primary-contrast;
  text-indent: -9999em;
  margin: 88px auto;
  position: relative;
  font-size: 11px;
  -webkit-transform: translateZ(0);
  -ms-transform: translateZ(0);
  transform: translateZ(0);
  -webkit-animation-delay: -0.16s;
  animation-delay: -0.16s;
}
.animated-loader:before,
.animated-loader:after {
  position: absolute;
  top: 0;
  content: '';
}
.animated-loader:before {
  left: -1.5em;
  -webkit-animation-delay: -0.32s;
  animation-delay: -0.32s;
}
.animated-loader:after {
  left: 1.5em;
}
@-webkit-keyframes load1 {
  0%,
  80%,
  100% {
    box-shadow: 0 0;
    height: 4em;
  }
  40% {
    box-shadow: 0 -2em;
    height: 5em;
  }
}
@keyframes load1 {
  0%,
  80%,
  100% {
    box-shadow: 0 0;
    height: 4em;
  }
  40% {
    box-shadow: 0 -2em;
    height: 5em;
  }
}

.del-btn{
 width:100%;
}

</style>



