
<template>
  <div class="kitchen_wrap">
   <transition name="slidein">
    <div class="kitchen-sink" v-if="kitchenSink">
      <span class="fa fa-chevron-right close-ks" @click="closeKitchenSink"></span>

      <div v-if="kitchenSink.type == 'contact'" class="col-md-12">
       <ContactForm :options="kitchenSink" />
      </div>

      <div v-if="kitchenSink.type == 'email' || kitchenSink.type =='phone'" class="col-md-12">
       <InfosForm :options="kitchenSink" />
      </div>

      <div v-if="kitchenSink.type == 'address'" class="col-md-12">
       <AddressesForm :options="kitchenSink" />
      </div>

    </div>
   </transition>
  </div>
</template>

<script>
import store from '../../store/store.js'
import ContactForm from './ContactForm.vue'
import InfosForm from './InfosForm.vue'
import AddressesForm from './AddressesForm.vue'

export default {
 components:{
  ContactForm,
  InfosForm,
  AddressesForm,
 },
 methods: {
   closeKitchenSink(e){
     store.dispatch("CLEAR_KITCHEN_SINK")
   }
 },
 computed:{
   kitchenSink: {
     get(){
       return store.getters.kitchenSink;
     }
   }
 },
}
</script>

<style lang="scss">
@import '../colors';
.kitchen-sink{
  -webkit-box-shadow: 0px 0px 5px 0px $box-shadow-color;
  -moz-box-shadow: 0px 0px 5px 0px $box-shadow-color;
  box-shadow: 0px 0px 5px 0px $box-shadow-color;
  background-color:$kitchen-sink;
  width:94%;
  height:100%;
  display:block;
  position:fixed;
  top:0px;
  z-index:7;
  right:0px;
  padding-right:20px;
  color:$font-color;

  .close-ks{
      background-color:$primary-mid;
      color:$font-color;
      font-size:30px;
      cursor:pointer;
      padding:20px;
  }

  label{
    color:$font-color;
  }

}

@keyframes slides {
  0% {
    transform: translateX(100%);
  }
  100% {
    transform: translateX(0);
  }
}

.slidein-enter-active {
  animation: slides .2s;
}
.slidein-leave-active {
  animation: slides .2s reverse;
}





</style>



