<template>
  <ul v-if="showSearchResults" id="contacts_list" class="data-list">
    <ContactItem
      v-for="contact in searchResults"
      :contact="contact"
    />
  </ul>
  <ul v-else id="contacts_list" class="data-list">
    <ContactItem
      v-for="contact in contacts"
      :contact="contact"
    />
  </ul>
</template>
<script >
import ContactItem from './ContactItem.vue'
import store from '../store/store.js'
export default {
  components: {
		ContactItem,
	},
  computed:{
      contacts: {
        get(){
          return store.getters.contacts
        }
      },
      showSearchResults: {
        get(){
          if(store.getters.searchResults.length > 0 ){
            return true;
          }
          else{
            return false;
          }
        }
      },
      searchResults: {
        get(){
          return store.getters.searchResults
        }
      },
  },
  data() {
    return {
      contact_list:[],
    }
  },
  mounted(){
    if(store.getters.authenticated){
        store.dispatch('LOAD_CONTACTS_LIST')
    }
  }
}
</script>
<style lang="scss">
@import 'colors';

#contacts_list{
  z-index:1;
  .info-btn{
    float:right;
    font-size:20px;
  }
}

</style>



