<template>
      <input v-on:keyup="placeSearch" type="text" placeholder="Search By Name" class="form-control">
</template>
<script>
import store from '../store/store.js'

export default {
  props:{
  },
  methods: {
    placeSearch:(e)=>{
      let searchTerm = "";
      if(e.target.value.length >2){
        searchTerm = e.target.value
      }

      store.dispatch("LOAD_SEARCH_RESULTS", searchTerm);
    }
  }
}

</script>
<style lang="scss">
#action_box{
  input{
    width:100%;
    border-radius:0px;
  }
}
</style>



