<template>
  <div id="app">

      <div v-if="authenticated">

        <KitchenSink/>



        <!--topnav logic-->
        <div v-if="detailStaged">
          <!--no top nav-->
        </div>
        <div v-else>
          <div class="util-bar-bottom hidden-md hidden-lg hidden-xl">
           <span class="fa fa-plus-circle" @click="openKitchenSink(
            {
             'type':'contact',
             'action':'create',
             'detail':{
              'first_name':'',
              'last_name':'',
              'birthday':'',
              'id':false
             }
            }
            , $event)" >
           </span>

           <span class="fa fa-sign-out pull-left" @click="signOut" >
           </span>
          </div>

          <div id="action_box">
            <div class="col-md-10 " style="margin-left:0px;padding-left:0px;padding-right:0px;margin-right:0px;">
            <ContactSearch />
            </div>
            <div class="col-md-2 hidden-sm hidden-xs " >
              <!-- <span class="fa fa-sign-out" ></span> -->

              <span class="fa fa-plus-circle pull-right" @click="openKitchenSink(
               {
                'type':'contact',
                'action':'create',
                'detail':{
                 'first_name':'',
                 'last_name':'',
                 'birthday':'',
                 'id':false
                }
               }
               , $event)" >
              </span>
              <span class="fa fa-sign-out pull-left" @click="signOut" >
              </span>

            </div>
          </div>
        </div>
        <!--topnav logic-->
        <div v-if="detailStaged">
          <ContactDetail />
        </div>
        <div v-else class="col-md-12">
            <ContactList/>
        </div>

      </div>
      <div v-else>
        <LoginForm></LoginForm>
      </div>
      <!--end else in authenticated condition-->
  </div>
</template>

<script>
import ContactList from './ContactList.vue'
import ContactSearch from './ContactSearch.vue'
import ContactDetail from './ContactDetail.vue'
import LoginForm from './forms/auth/LoginForm.vue'
import KitchenSink from './forms/KitchenSink.vue'

import store from '../store/store.js'

export default {
  name: 'app',
  components:{
    ContactList,
    ContactSearch,
    ContactDetail,
    LoginForm,
    KitchenSink,
  },
  methods:{
   openKitchenSink:(details, e)=>{
     store.dispatch("LOAD_KITCHEN_SINK", details );
   },
   signOut(){
    localStorage.removeItem('authToken');
    location.reload();
   }
  },
  computed:{
   loading:{
     get(){
      return store.getters.loading;
     }
   },
   authenticated: {
      get(){
        return store.getters.authenticated;
      }
   },
   detailStaged: {
      get(){
        return store.getters.detailStaged
      }
   },
  },
  async mounted(){
    let tokenIsValid = await store.dispatch('CHECK_TOKEN_IS_VALID');
    console.log("finished loading")
    console.log(tokenIsValid)
    if(tokenIsValid){
      store.dispatch('LOAD_ACCOUNT_DETAILS');
    }
  }
}
</script>



<style lang="scss">
@import 'colors';



body{
  font-family: 'Roboto', sans-serif;
  background-color:$base;
  overflow-x:hidden;

}

.stnd-form-wrap{
 //look in to the proper way to animate vue components.
 @keyframes slideInFromTop {
   0% {
     transform: translateY(-100%);
   }
   100% {
     transform: translateY(0);
   }
 }
 form{
  color:$font-color;
 }
 input{
  border-radius:0px;
 }

 .error-list{
  margin:0px;
  padding:0px;

  li.error{
   /* This section calls the slideInFromLeft animation we defined above */
   animation: .1s ease-out 0s 1 slideInFromTop;
   list-style:none;
   background-color:$primary-mid;
   color:$font-color;
   margin-bottom:10px;
   padding:10px;
  }
 }


}

.animated-loader,
.animated-loader:before,
.animated-loader:after {
  background: $primary-contrast;
  -webkit-animation: load1 1s infinite ease-in-out;
  animation: load1 1s infinite ease-in-out;
  width: 1em;
  height: 4em;
}
.animated-loader {
  color: $primary-contrast;
  text-indent: -9999em;
  margin: 88px auto;
  position: relative;
  font-size: 11px;
  -webkit-transform: translateZ(0);
  -ms-transform: translateZ(0);
  transform: translateZ(0);
  -webkit-animation-delay: -0.16s;
  animation-delay: -0.16s;
}
.animated-loader:before,
.animated-loader:after {
  position: absolute;
  top: 0;
  content: '';
}
.animated-loader:before {
  left: -1.5em;
  -webkit-animation-delay: -0.32s;
  animation-delay: -0.32s;
}
.animated-loader:after {
  left: 1.5em;
}
@-webkit-keyframes load1 {
  0%,
  80%,
  100% {
    box-shadow: 0 0;
    height: 4em;
  }
  40% {
    box-shadow: 0 -2em;
    height: 5em;
  }
}
@keyframes load1 {
  0%,
  80%,
  100% {
    box-shadow: 0 0;
    height: 4em;
  }
  40% {
    box-shadow: 0 -2em;
    height: 5em;
  }
}


html{
  overflow-x:hidden;
  padding-top:52px;
  padding-bottom:60px;
  input[type="color"],
  input[type="date"],
  input[type="datetime"],
  input[type="datetime-local"],
  input[type="email"],
  input[type="month"],
  input[type="number"],
  input[type="password"],
  input[type="search"],
  input[type="tel"],
  input[type="text"],
  input[type="time"],
  input[type="url"],
  input[type="week"],
  textarea {
    font-size: 16px;
  }
}
h1{
  font-size:20px;
}

.no-p{
  padding:0px;
}

.no-m{
  padding:0px;
}

.no-m-r{
  margin-right:0px;
}

.no-m-l{
  margin-left:0px;
}

.no-p-r{
  padding-right:0px;
}

.no-p-l{
  padding-left:0px;
}

.btn{
  border-radius:0px;
}
@keyframes slideInFromLeft {
  0% {
    transform: translateX(100%);
  }
  100% {
    transform: translateY(0);
  }
}

.util-bar-bottom{
  text-align:center;
  .fa{
    cursor:pointer;
    font-size:40px;
    display:inline-block;
    color:$font-color;

    margin-top:10px;
  }
  .fa-sign-out{
   float:left;
   color:$red-tone;
   position:absolute;
   bottom:20px;
   left:20px;
   font-size:15px;

  }
  background-color:$base;
  position:fixed;
  bottom:0px;
  z-index:3;
  height:60px;
  width:100%;
}

#action_box{
 -webkit-box-shadow: 0px 0px 5px 0px $box-shadow-color;
 -moz-box-shadow: 0px 0px 5px 0px $box-shadow-color;
 box-shadow: 0px 0px 5px 0px $box-shadow-color;
 background-color:$kitchen-sink;
  background-color:$base;
  height:52px;
  width:100%;
  padding-left:15px;
  padding-right:15px;
  padding-top:10px;
  position:fixed;
  top:0px;
  z-index:3;
  .fa-plus-circle{
    font-size:30px;
    color:$font-color;
    display:inline-block;

    margin-left:10px;
    cursor:pointer;
  }
  .fa-sign-out{
   float:left;
   color:$red-tone;
   position:absolute;
   &:hover{
    opacity:.5;
   }
   left:20px;
   padding:10px;
   font-size:15px;
   cursor:pointer;
  }
}

.data-list{
  padding:0px;
  li:nth-child(odd) {background: $mid-light;}
  li:nth-child(even) {background: $mid-dark;}
  li{
    list-style:none;
    display:block;
    margin-bottom:2px;
    padding:15px;
    cursor:pointer;
    color:$font-color;
    &:hover{
      opacity:.7;
    }
  }
}

.delete-btn{
 background-color:$red-tone;
 color:$primary-light;
 padding:15px;
 width:100%;
}


</style>



