<template>
<!-- <template> -->
 <div class="col-md-12 info-detail-wrap">
  <h3 v-if="options.type == 'email'" class="info-detail-title">Email</h3>
  <h3 v-if="options.type == 'phone'" class="info-detail-title">Phone</h3>
  <h3 v-if="options.type == 'address'" class="info-detail-title">Address</h3>
  <ul class="data-list">

    <li v-if="options.type == 'email'"
    @click="openKitchenSink({
      type:options.type,
      detail:detailStaged,
      id:email.id,
      value:email.value
     })"
     v-for="email in emails"
     class="contact-detail-wrap edit-info">
      <p style="display:inline;"><span  class="fa fa-envelope"></span> {{email.value}} </p>
      <a class="btn btn-default pull-right info-action" v-on:click.stop :href="`mailto:${email.value}`">Send Email</a>
    </li>
    <li v-if="options.type == 'email' && emails.length == 0">
      <p class="empty-info">No Email Addresses Yet. Add One</p>
    </li>




    <li v-if="options.type == 'phone'"

    @click="openKitchenSink({
      type:options.type,
      detail:detailStaged,
      id:phone.id,
      value:phone.value
     })"

     v-for="phone in phones"
     class="contact-detail-wrap edit-info">
      <p style="display:inline;"><span  class="fa fa-phone"></span> {{prettyPhone(phone.value)}}</p>
      <a class="btn btn-default pull-right info-action" v-on:click.stop :href="`tel:${phone.value}`">Call</a>
    </li>
    <li v-if="options.type == 'phone' && phones.length == 0">
      <p class="empty-info">No Phone Numbers Yet. Add One</p>
    </li>




    <li v-if="options.type == 'address'"

    @click="openKitchenSink({
      type:options.type,
      detail:detailStaged,
      id:address.id,
      address:address
     })"

     v-for="address in addresses"
     class="contact-detail-wrap edit-info">
      <p style="display:inline;"><span  class="fa fa-map-marker"></span> {{address.address_line1}}, {{address.city}} {{address.state_province}}</p>
    </li>

    <li v-if="options.type == 'address' && addresses.length == 0">
      <p class="empty-info">No Adresses Yet. Add One</p>
    </li>


  </ul>
  <span @click="openKitchenSink({
    type:options.type,
    detail:detailStaged,
    id:false,
    value:''
   })"
  class="fa fa-plus-circle add-info"></span>


  </div>
</template>
<script>
import store from '../store/store.js'
export default {
 props:{
   options:{
     type: Object,
     required: true
   },
 },
 ///this needs to be dependent on computed properties that watch changes in the vuex store. not the data. needs to be reactive.
 methods: {
   openKitchenSink(options){
    store.dispatch('LOAD_KITCHEN_SINK', options)
   },
   prettyPhone(num) {
    var str = num.toString();
    var matched = str.match(/\d+\.?\d*/g);
    // 10 digit
    if (matched.length === 3) {
        return '(' + matched[0] + ') ' + matched[1] + '-' + matched[2];
    // 7 digit
    } else if (matched.length === 2) {
        return matched[0] + '-' + matched[1];
    }
    // no formatting attempted only found integers (i.e. 1234567890)
    else if (matched.length === 1) {
        // 10 digit
        if (matched[0].length === 10) {
            return '(' + matched[0].substr(0, 3) + ') ' + matched[0].substr(3, 3) + '-' + matched[0].substr(6);
        }
        // 7 digit
        if (matched[0].length === 7) {
            return matched[0].substr(0, 3) + '-' + matched[0].substr(3);
        }
    }
    // Format failed, return number back
    return num;
   },
  },
  computed: {
    detailStaged: ()=>{
     return store.getters.detailStaged;
    },
    emails: ()=>{
     return store.getters.detailStaged.emails;
    },
    phones: ()=>{
     return store.getters.detailStaged.phones;
    },
    addresses: ()=>{
     return store.getters.detailStaged.addresses;
    }
  }
}

</script>
<style lang="scss">
.info-detail-wrap{
 margin-bottom:20px;
 .empty-info{
  font-size:18px;
 }
 .add-info{
  font-size:30px;
  margin-left:10px;
  cursor:pointer;
 }
}
.contact-detail-wrap{
    .fa{
      position:relative;
      top:3px;
      display:inline-block;
      font-size:30px;
      margin-right:10px;
    }
}
</style>



